							"text": "Mason Circle",
							"sceneId": "circle",
							"targetYaw": -23,
							"targetPitch": 2
						}
					]
				}
			},
			// panoramaInfo: {
			// 	Pitch: 0,
			// 	Yaw: 0,
			// 	Hfov: 0
			// }
    };
  },
	computed: {
		streetViewList() {
			if(!polyLineInfo.hasOwnProperty(this.listQuery.distIndex)) return {};
			const lineInfoList = polyLineInfo[this.listQuery.distIndex];

      let streetViewList = {};
      for(const lineInfo of lineInfoList) {
        lineInfo.forEach((info, index) => {
          streetViewList[info.fileName] = {
            "type": "equirectangular",
            "panorama": `${this.panoramaUrl}/${this.listQuery.distIndex}/${info.fileName}.jpg`,
            "hfov": 110,
            "compass": true,
            "northOffset": info.azimuth,
            "horizonRoll": 4,
            "autoLoad": true
          };

          let hotSpots = [];
          if(index >= 0 && index < lineInfo.length - 1) {
            hotSpots.push({
              "pitch": -5,
              "yaw": 0,
              "type": "scene",
              "text": lineInfo[index+1].fileName,
              "sceneId": lineInfo[index+1].fileName,
            })
          }

          if(index > 0 && index < lineInfo.length) {
            hotSpots.push({
              "pitch": -5,
              "yaw": 180,
              "type": "scene",
              "text": lineInfo[index-1].fileName,
              "sceneId": lineInfo[index-1].fileName,
              "targetYaw": 180
            });
          }

          streetViewList[info.fileName]["hotSpots"] = hotSpots;
        })
      }

			return streetViewList;
		},
		PCIKMLUrl() {
			// kml網址: ${assetUrl}/kml/PCI/${年月字串}/3_1_${區域號}_${流水號}P${PCI等級}_${單位號}.KML
			// 單位號： 目前都為1 (??)

			const dateStr = this.listQuery.year + String(this.listQuery.month).padStart(2, '0');
			let urlObj = {};
			for(const level of [...Object.keys(this.PCILevel), ...Object.keys(this.areaList)]) {
				urlObj[level] = `${this.assetUrl}/kml/PCI/${dateStr}/3_1_${this.listQuery.distIndex}_P${level}_${kmlFileLastNum[dateStr]}.KML`;
				// urlObj[level] = `${this.assetUrl}/kml/PCI/200002/3_1_${this.listQuery.distIndex}_P${level}_12.KML`;
				// urlObj[level] = `${this.assetUrl}/kml/PCI/202205/3_1_${this.listQuery.distIndex}_P${level}_23.KML`;
			}

			return urlObj;
		},
		caseKMLUrl() {
			// kml網址: ${assetUrl}/kml/PCI/${年月字串}/3_1_${區域號}_${流水號}pointP${PCI等級}_${單位號}.KML
			// 單位號： 目前都為1

			const dateStr = this.listQuery.year + String(this.listQuery.month).padStart(2, '0');
			let urlObj = {};
			for(const level of [...Object.keys(this.PCILevel), ...Object.keys(this.areaList)]) {
				urlObj[level] = `${this.assetUrl}/kml/PCI/${dateStr}/3_1_${this.listQuery.distIndex}_1pointP_${level}_${kmlFileLastNum[dateStr]}.KML`;
			}
			return urlObj;
		},
		districtListFilter() {
			return Object.fromEntries(Object.entries(this.districtList).filter(([key, _]) => key != this.listQuery.distIndex));
		},
		caseTypeListFilter() {
			return this.listQuery.caseType ? Object.fromEntries(Object.entries(this.caseTypeList).filter(([key, _]) => key == this.listQuery.caseType)) : this.caseTypeList;
		},
		PCIInfoTotal() {
			const total = this.PCIInfoList.length | 0;
			const areaTotal = this.PCIInfoList.reduce((curr, prev) => curr += Number(prev.area), 0);
			const scoreAverage = this.PCIInfoList.reduce((curr, prev) => curr += Number(prev.score), 0) / total;

			return { total, areaTotal, scoreAverage: scoreAverage.toFixed(2) }
		}
	},
  mounted() {
		//PCILevel預設全開
		this.listQuery.PCILevel = this.PCILevelOrder;

    // window.addEventListener("load", () => {
      loader.load().then(() => this.initMap());
			this.panorama = pannellum.viewer('panorama', { 
				// "default": {
				// 	"firstScene": "circle",
				// 	"sceneFadeDuration": 1000
				// },
				scenes: {}
			})

			for(const sceneId in this.panoramaList) {
				this.panorama.addScene(sceneId, this.panoramaList[sceneId]);
			}

			this.panorama.on('load', () => { 
				this.panorama.resize();
			});

			this.$el.querySelector(".pnlm-compass").addEventListener('click', () => {
				const northOffset = this.panorama.getNorthOffset();
				this.panorama.setYaw(-northOffset);
			});

			// this.panorama.on('animatefinished', () => { 
			// 	this.panoramaInfo = { Pitch: this.panorama.getPitch(), Yaw: this.panorama.getYaw(), Hfov: this.panorama.getHfov() };
			// });

			// this.panorama.on('mouseup', (e) => { 
			// 	const randomNum = Math.floor(Math.random() * Object.keys(this.panoramaList).length);
			// 	const sceneId = Object.keys(this.panoramaList)[randomNum];

			// 	this.panorama.addHotSpot({
      //     id: 'hot' + Date.parse(new Date()),
      //     type: 'scene',
      //     pitch: this.panorama.mouseEventToCoords(e)[0],
      //     yaw: this.panorama.mouseEventToCoords(e)[1],
      //     text: '22223',
      //     clickHandlerArgs: '2xxxxx',
      //     sceneId: sceneId,
      //     scale: true,
      //     clickHandlerFunc: (e, data) => {
			// 			console.log(this.panorama.mouseEventToCoords(e));
      //       console.log(e);
      //       console.log(data);
      //     },
      //   });
			// });
		// });
  },
	watch: {
		"listQuery.activePCILayer"(val) {
			if(val) {
				this.listQuery.activePCI = true;
				this.listQuery.activeCase = false;
				if(this.listQuery.PCILevel.length == 0) this.listQuery.PCILevel = this.PCILevelOrder;
			} else {
				this.listQuery.activePCI = this.listQuery.activeCase = false;
				this.listQuery.PCILevel = [];
			}

			this.setPCILayer(); 
			this.setCaseLayer();
		} 
	},
  methods: {
    // init google map
    initMap() {
      // 預設顯示的地點：台北市政府親子劇場
      // const location = {
      //   lat: 25.0374865, // 經度
      //   lng: 121.5647688, // 緯度
      // };

			const location = this.districtList[this.listQuery.distIndex].position;
			const stylesArray = [
        {
          stylers: [{ visibility: "on" }],
        },
        {
          featureType: "poi",
          elementType: "all",
          stylers: [{ visibility: "off" }],
        },
        {
          featureType: "transit",
          elementType: "all",
          stylers: [{ visibility: "off" }],
        },
      ];

      // 建立地圖
      this.map = new google.maps.Map(this.$refs.map, {
        center: location, // 中心點座標
        zoom: 13, // 1-20，數字愈大，地圖愈細：1是世界地圖，20就會到街道
        /*
          roadmap 顯示默認道路地圖視圖。
          satellite 顯示 Google 地球衛星圖像。
          hybrid 顯示正常和衛星視圖的混合。
          terrain 顯示基於地形信息的物理地圖。
        */
        // mapTypeId: "roadmap",
				fullscreenControl: false,
				mapTypeControl: false,
				styles: stylesArray
      });

			// 掛載place server
			this.placeServer = new google.maps.places.PlacesService(this.map);
			
			// 掛載 PCI KmlLayer
			for(const level of [...Object.keys(this.PCILevel), ...Object.keys(this.areaList)] ) {
				this.PCILayer[level] = new google.maps.KmlLayer({
					url: this.PCIKMLUrl[level],
					map: this.map,
					suppressInfoWindows: true,
					preserveViewport: true
				});
				// this.PCILayer.setMap(this.map);

				this.PCILayer[level].addListener("click", (event) => {
					//<div style="font-family: Roboto,Arial,sans-serif; font-size: small"><div style="font-weight: 500; font-size: medium; margin-bottom: 0em">ID：10345，區塊編號：0101362000071，地點：復興北路地下道，面積：252㎡，道路編號：007-順向，PCI統計日期：2021/09/01~2021/09/08，PCI程度：很好，PCI分數：100</div></div>
					//@@@6923###95.4
					//@@@1404###style="font-family: Roboto,Arial,sans-serif; font-size: small">ID：1404，區塊編號：0201405000011，地點：復興北路190巷，面積：224㎡，道路編號：001-順向，上月PCI：71.1，本月PCI：73.1，PCI程度：好

					// let content = event.featureData.infoWindowHtml;
					// console.log(content);

					//區塊ID
					// let Snum1 = (content.indexOf("：",0) + 1);
					// let Enum1 = content.indexOf("，",0);
					// let Nnum1 = Enum1 - Snum1;
					// let content1 = content.substr(Snum1, Nnum1);

					//PCI分數
					// let Snum2 = (content.indexOf("PCI分數：",0) + 6);
					// let Enum2 = content.indexOf("</div>",0);
					// let Nnum2 = Enum2 - Snum2;
					// let content2 = content.substr(Snum2, Nnum2);
					// content = content1 + '###' + content2;

					// console.log(event.featureData);
					const description = event.featureData.name;
					// console.log(description);

					//ID：5971，區塊編號：0101281000030，地點：龍江路429巷，面積：172㎡，道路編號：003-路口，PCI統計日期：2022/05/01~2022/05/25，PCI程度：很好，PCI分數：100
					const content = description
						.split(/ID：(\d*)，區塊編號：(\d+)，地點：(.*)，面積：(\d*)㎡，道路編號：(.*)，PCI統計日期：.*?，PCI分數：(\d*)/s)
						.filter((str) => str.length != 0);
					// console.log(content);

					const isSame = this.PCIInfoList.some(l => l.id == content[0]);
					if(!isSame) {
						this.PCIInfoList.push({
							id: content[0],
							num: content[1],
							block: content[2],
							place: content[4],
							area: content[3],
							score: content[5]
						})
					}
				});
			}

			// 掛載 Case KmlLayer
			for(const level of [...Object.keys(this.PCILevel), ...Object.keys(this.areaList)]) {
				this.caseLayer[level] = new google.maps.KmlLayer({
					map: this.map,
					// suppressInfoWindows: true,
					preserveViewport: true
				});

				// this.caseLayer[level].addListener("click", (event) => {
				// 	console.log(event);
				// 	console.log(event.featureData);
					
				// 	const description = event.featureData.description.replace('border="1"', 'border="0"');
				// 	// console.log(description);

				// 	const infoWindow = new google.maps.InfoWindow({
				// 		position: event.latLng,
				// 		content: description,
				// 		// pixelOffset: event.pixelOffset,
				// 	});

				// 	infoWindow.open(this.map);
				// 	console.log(infoWindow.getContent());
				// });
			}
			
			// 掛載 Marker
			for(let markerName in this.markerList) {
				this.markers[markerName] = new google.maps.Marker({
					title: markerName,
					position: this.markerList[markerName],
					// map: this.map
				});

				this.markers[markerName].addListener("click", (event) => {
					const sceneId = this.markers[markerName].getTitle();
					console.log(sceneId);
					this.showPanoramaLayer(sceneId);
				});
			}
		},
		setMapType(type) {
			this.map.setMapTypeId(type);
			this.listQuery.mapType = type;
		},
		search() {
			if(this.listQuery.searchText.length == 0) this.listQuery.searchText = "台北市";
			for(let index in this.districtList) {
				if(this.listQuery.distIndex != index && this.districtList[index].name.indexOf(this.listQuery.searchText) != -1) this.setDistIndex(index);
			}

			// NOTE: Place Server 需要 google console 設定
			// const request = {
			// 	fields: ['ALL'],
			// 	query: this.listQuery.searchText
			// };
			// this.placeServer.findPlaceFromQuery(request, (result, status) => {
			// 	console.log(result, status);
			// 	if(status === google.maps.places.PlacesServiceStatus.OK && results) 
			// 		this.map.setCenter(results[0].geometry.location);
			// });
		},
		setLayer(layerType) {
			this.showLayerMenu = false;
			
			switch(layerType) {
				case "PCI":
					this.clearAllLayer();
					this.listQuery.activePCILayer = true;
					this.listQuery.activePanoramaLayer = false;
					this.setPCILayer();
					break;
				case "buildings":
					break;
				case "sidewalk":
					break;
				case "panorama":
					// this.showPanoramaLayer();
					this.clearAllLayer();
					this.listQuery.activePCILayer = false;
					this.listQuery.activePanoramaLayer = true;
					this.PCIInfoList = [];
					this.setPanoramaLayer();
					break;
			}
			// this.listQuery.layerType = layerType;
		},
		setPCILayer(level) {
			for(const [_, layer] of Object.entries(this.PCILayer)) layer.setMap(null);
			if(!this.listQuery.activePCI) return;

			// level == -2，全部; level == -1，清除 ; level >= 0，先清除在讀入
			if(level == -2) this.listQuery.PCILevel = this.PCILevelOrder;
			if(level >= -1) this.listQuery.PCILevel = [];
			if(level > -1) this.listQuery.PCILevel = [ level ];

			//如果包含 變差區域(PCI LV7) 和 變好區域(PCI LV8)，應先剔除
			const PCILevelFilter = this.listQuery.PCILevel.filter(level => ![7, 8].includes(level));
			if(PCILevelFilter.length > 0) this.listQuery.PCILevel = PCILevelFilter;

			for(const level of this.listQuery.PCILevel) {
				console.log(this.PCIKMLUrl[level]);
				this.PCILayer[level].setUrl(this.PCIKMLUrl[level]);
				if(this.PCILayer[level].getMap() == null) this.PCILayer[level].setMap(this.map);
			}

			this.listQuery.layerType = "PCI";
		},
		setCaseLayer() {
			for(const [_, layer] of Object.entries(this.caseLayer)) layer.setMap(null);
			if(!this.listQuery.activeCase) return;

			for(const level of this.listQuery.PCILevel) {
				console.log(this.caseKMLUrl[level]);
				this.caseLayer[level].setUrl(this.caseKMLUrl[level]);
				if(this.caseLayer[level].getMap() == null) this.caseLayer[level].setMap(this.map);
			}
		},
		setDistIndex(distIndex) {
			this.showDistrictMenu = false;
			this.clearAllLayer();

			if(distIndex != undefined) this.listQuery.distIndex = Number(distIndex);
			this.listQuery.searchText = this.districtList[this.listQuery.distIndex].name;

			// 改變位置
			const location = this.districtList[this.listQuery.distIndex].position;
			this.map.setCenter(location);

			this.setPCILayer();
			this.setCaseLayer();
			this.setPanoramaLayer();
		},
		setPanoramaLayer() {
      for(const polyLineList of Object.values(this.polyLine)) for(const polyLine of Object.values(polyLineList)) polyLine.setMap(null);
			for(const markers of Object.values(this.markers)) markers.setMap(null);
			if(!this.listQuery.activePanoramaLayer) return;

			// this.polyLine.setMap(this.map);
			this.createPolyLine();
			for(const markers of Object.values(this.markers)) markers.setMap(this.map);
			this.listQuery.layerType = "panorama";
		},
		createPolyLine() {
      this.polyLine[this.listQuery.distIndex] = [];
			const lineInfo = polyLineInfo[this.listQuery.distIndex];
			if(!lineInfo) return;

			// 掛載panorama scene
			for(const sceneId of this.prevSceneId) this.panorama.removeScene(sceneId);
			this.prevSceneId = [];
			for(const sceneId in this.streetViewList) {
				this.panorama.addScene(sceneId, this.streetViewList[sceneId]);
				this.prevSceneId.push(sceneId);
			}

			// 掛載 polyline Layer(street view)
      if(this.polyLine[this.listQuery.distIndex].length > 0) {
        for(const polyLine of this.polyLine[this.listQuery.distIndex]) polyLine.setMap(this.map);
      } else {
        lineInfo.forEach((polyInfo, index) => {
          this.polyLine[this.listQuery.distIndex].push(new google.maps.Polyline({
            path: polyInfo.map(info => info.position),
            geodesic: true,
            strokeColor: "#2196F3",
            strokeOpacity: 1,
            strokeWeight: 3,
            map: this.map,
          }));

          this.polyLine[this.listQuery.distIndex][index].addListener("click", (event) => {
            const pointPos = event.latLng.toJSON();
            const posList = polyLineInfo[this.listQuery.distIndex][index].map(info => ({ ...info.position, sceneId: info.fileName }));
            const minDistObj = posList.reduce((minDistObj, curr) => {
              const distance = Math.sqrt(Math.pow((pointPos.lat - curr.lat), 2) + Math.pow((pointPos.lng - curr.lng), 2));
              if(minDistObj.dist > distance) {
                minDistObj.dist = distance;
                minDistObj.sceneId = curr.sceneId;
              }
              return minDistObj;
            }, { dist: Infinity, sceneId: ""})
            this.showPanoramaLayer(minDistObj.sceneId);
          });
        })
      }
		},
		showPanoramaLayer(sceneId) {
			if(this.showPanorama) {
				this.showPanorama = false;
				this.listQuery.layerType = "PCI";
				return;
			}

			console.log(sceneId);
			if(sceneId == undefined) {
				const randomNum = Math.floor(Math.random() * Object.keys(this.panoramaList).length);
				sceneId = Object.keys(this.panoramaList)[randomNum];
			} 

			console.log(sceneId);

			this.panorama.loadScene(sceneId);
			this.showPanorama = true;
		},
		clearAllLayer() {
			//清除PCI
			for(const [_, layer] of Object.entries(this.PCILayer)) layer.setMap(null);
			for(const [_, layer] of Object.entries(this.caseLayer)) layer.setMap(null);

			//清除markers
      for(const polyLineList of Object.values(this.polyLine)) for(const polyLine of Object.values(polyLineList)) polyLine.setMap(null);
			for(const markers of Object.values(this.markers)) markers.setMap(null);
		},
		delPCIInfo(item) {
			const index = this.PCIInfoList.indexOf(item);
			this.PCIInfoList.splice(index, 1);
		}
  },
};
</script>

